import { AddIcon } from '@chakra-ui/icons';
import { Box, Button, Stack, Text, useToast } from '@chakra-ui/react';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { getSender } from '../config/ChatLogic';
import { ChatState, ChatType, UserType } from '../context/ChatContext';
import ChatLoading from './ChatLoading';
import GroupChatModal from './Miscellaneous/GroupChatModal';

const MyChats = ({ fetchAgain }: { fetchAgain: boolean }) => {
    const [loggedUser, setLoggedUser] = useState<UserType>();
    const { user, chats, saveChats, selectedChat, saveSelectedChat } = ChatState();
    const toast = useToast();

    const fetchChat = async () => {
        if (!user._id) return;
        try {
            const config = {
                headers: {
                    Authorization: `Bearer ${user?.token}`
                }
            };

            const { data } = await axios.get(`/api/chat`, config);
            saveChats(data);
        } catch (error) {
            toast({
                title: 'Error Occured!',
                description: 'Failed to load the chats',
                status: 'error',
                duration: 5000,
                isClosable: true,
                position: 'bottom-left'
            });
        }
    };

    useEffect(() => {
        setLoggedUser(JSON.parse(localStorage.getItem('userInfo') as string));
        fetchChat();
    }, [fetchAgain, user]);

    return (
        <Box
            display={{ base: selectedChat ? 'none' : 'flex', md: 'flex' }}
            flexDir="column"
            alignItems={'center'}
            p={3}
            bg="white"
            w={{ base: '100%', md: '31%' }}
            borderRadius="lg"
            borderWidth={'1px'}
        >
            <Box pb={3} px={3} fontSize={{ base: '28px', md: '30px' }} fontFamily="Work sans" display={'flex'} w="100%" justifyContent={'space-between'} alignItems="center">
                My Chats
                <GroupChatModal>
                    <Button display={'flex'} fontSize={{ base: '17px', md: '10px', lg: '17px' }} rightIcon={<AddIcon />}>
                        New Group Chat
                    </Button>
                </GroupChatModal>
            </Box>
            <Box display={'flex'} flexDir="column" p={3} bg="#f8f8f8" w={'100%'} h="100%" borderRadius={'lg'} overflowY="hidden">
                {chats ? (
                    <Stack overflowY="scroll">
                        {chats.map((chat: ChatType) => (
                            <Box
                                onClick={() => saveSelectedChat(chat)}
                                cursor="pointer"
                                bg={selectedChat?._id === chat._id ? '#3882ac' : '#e8e8e8'}
                                color={selectedChat?._id === chat._id ? 'white' : 'black'}
                                px={3}
                                py={2}
                                borderRadius="lg"
                                key={chat._id}
                            >
                                <Text>{loggedUser && !chat.isGroupChat ? getSender(loggedUser, chat.users) : chat.chatName}</Text>
                            </Box>
                        ))}
                    </Stack>
                ) : (
                    <ChatLoading />
                )}
            </Box>
        </Box>
    );
};

export default MyChats;
